* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", Arial, sans-serif;
  background: linear-gradient(135deg, #f5f7fa, #c3cfe2);
  color: #222;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Header styling */
.App-header {
  text-align: center;
  padding: 2rem 1rem;
  background: #007bff;
  color: white;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.App-header h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.subtitle {
  font-size: 1.2rem;
  opacity: 0.9;
}

/* Projects container */
main {
  flex: 1;
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* Project grid (responsive) */
.projects-grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(650px, 1fr));
}

/* Project cards */
.project-card {
  background: white;
  border-radius: 15px;
  padding: 1rem;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

/* Adding some more rules*/
.project-card img {
  width: 100%;
  height: 320px;   
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 0.5rem;
}

/* Links */
.project-card a {
  text-decoration: none;
  color: #007bff;
  font-weight: 500;
  display: inline-block;
  margin-top: 0.5rem;
}

.project-card a:hover {
  text-decoration: underline;
}

/* Footer */
.App-footer {
  text-align: center;
  padding: 1rem;
  background: #f1f1f1;
  border-top: 1px solid #ddd;
  font-size: 0.9rem;
  color: #555;
}
